<template>
  <div class="ScanningFilter">
    <HeaderNavigation />
    <div class="ScanningFilterBox">
      <h1>掃描濾心QR Code</h1>
      <h2>淨水器機型：P0670</h2>
      <div class="ScanningFilterBoxContent">
        <img src="@/assets/image/ScanningFilterA.png" alt="" />
        <div class="hostNumber">
          <em>主機製號</em> 
          {{ this.$route.query.WaterMachineMfno }}
        </div>
        <van-field
          v-model="filterNumber"
          label="濾心製號"
          :disabled="checked"
          placeholder="請填寫濾心製號"
        />
        <!-- 新增选框 -->
        <van-checkbox v-model="checked" icon-size="16px">無製造號碼，請勾選</van-checkbox>
        <div class="ScanningFilterBoxContentTitle">
          <p v-if="status">此欄不得為空</p>
          <p v-if="statusA">濾心製號不合法</p>
          <van-uploader
            class="ScanningFilterBoxContentTitleSpan"
            :after-read="beforeReadCode"
          >
            <span>拍照上傳</span>
          </van-uploader>
        </div>
        <van-field
          style="margin-top: 20px"
          v-model="Price"
          label="實收金額"
          placeholder="請填寫實收金額"
        />
        <div v-if="PriceStatus" class="ScanningFilterBoxContentTitles">
          <p>此欄不得為空</p>
        </div>
        <div class="ScanningFilterBoxContentButtom" @click="submit()">
          儲存
        </div>
      </div>
    </div>
    <!-- 完成弹窗 -->
    <van-popup v-model="inHandShow" class="vanPopupinHand">
      <img draggable="false" src="@/assets/image/inHand.png" alt="" />
      <h2>處理中，請稍候...</h2>
    </van-popup>
    <!-- 失败弹窗 -->
    <van-popup v-model="failedShow" class="vanPopupfailed">
      <img draggable="false" src="@/assets/image/failed.png" alt="" />
      <h2>辨識失敗</h2>
      <h3>請再次嘗試，或洽總公司處理</h3>
      <div @click="failedHide()">我知道了</div>
    </van-popup>
  </div>
</template>

<script>
import { POST_GetMFNO } from "@/utils/api";
import HeaderNavigation from "@/components/HeaderNavigation";
export default {
  name: "ScanningFilter",
  components: {
    HeaderNavigation
  },
  data() {
    return {
      checked: false,
      failedShow: false,
      inHandShow: false,
      status: false,
      statusA: false,
      PriceStatus: false,
      filterNumber: "Z",
      Price: ""
    };
  },
  watch: {
    checked: function() {
      if (this.checked) {
        this.filterNumber = 'Z'
      }
    },
    filterNumber: function() {
      if (!this.filterNumber && !this.checked) {
        this.status = true;
      } else {
        this.status = false;
      }
    },
    Price: function() {
      if (!this.Price) {
        this.PriceStatus = true;
      } else {
        this.PriceStatus = false;
      }
    }
  },
  methods: {
    beforeReadCode(file) {
      let UofIds = new FormData();
      UofIds.append("file", file.file);
      this.inHandShow = true;
      POST_GetMFNO(UofIds).then(
        response => {
          this.filterNumber = response.Data;
          this.inHandShow = false;
        },
        // eslint-disable-next-line no-unused-vars
        err => {
          this.inHandShow = false;
          this.failedShow = true;
        }
      );
    },
    submit() {
      if (!this.filterNumber) {
        this.status = true;
      } else {
        this.status = false;
      }
      var numReg = /^1BE/;
      if (this.filterNumber != 'Z') {
        if (!numReg.test(this.filterNumber)) {
          this.statusA = true;
          return false;
        } else {
          this.statusA = false;
        }
        if (this.filterNumber.length > 14 || this.filterNumber.length < 13) {
          this.statusA = true;
          return false;
        } else {
          this.statusA = false;
        }
      } else {
        this.statusA = false;
      }
      if (!this.Price) {
        this.PriceStatus = true;
      } else {
        this.PriceStatus = false;
      }
      if (!this.Price || !this.filterNumber) {
        return false;
      }
      // this.$router.push({
      //   name: "Signature",
      //   query: {
      //     Mfno: this.filterNumber,
      //     value: this.$route.query.value,
      //     CustId: this.$route.query.CustId,
      //     type: "replace",
      //     Price: this.Price
      //   }
      // });
      let dataForm = {
        CustId: this.$route.query.CustId,
        Mfno: this.filterNumber,
        Price: this.Price
      };
      // 濾芯更換儲存數據到vuex
      // 濾芯延期清空數據
      if (this.$route.query.value == 1) {
        this.$store.commit("replace/setReplace1", dataForm);
        this.$store.commit("extension/setExtension1", {});
      } else if (this.$route.query.value == 2) {
        this.$store.commit("replace/setReplace2", dataForm);
        this.$store.commit("extension/setExtension2", {});
      } else if (this.$route.query.value == 3) {
        this.$store.commit("replace/setReplace3", dataForm);
        this.$store.commit("extension/setExtension3", {});
      } else if (this.$route.query.value == 4) {
        this.$store.commit("replace/setReplace4", dataForm);
        this.$store.commit("extension/setExtension4", {});
      } else if (this.$route.query.value == 5) {
        this.$store.commit("replace/setReplace5", dataForm);
        this.$store.commit("extension/setExtension5", {});
      }
      // 跳回保養頁面
      this.$router.push({
        path: "/InstallReplacement/InstallBasics",
        query: {
          CustId: this.$route.query.CustId
        }
      });
    },
    failedHide() {
      this.failedShow = false;
    }
  }
};
</script>

<style lang="scss">
.ScanningFilter {
  width: 100%;
  margin-bottom: 40px;
  .ScanningFilterBox {
    padding: 30px 5.33% 0;
    h1 {
      font-size: 22px;
      font-weight: 500;
      color: rgba(61, 61, 61, 1);
      line-height: 30px;
    }
    h2 {
      font-size: 12px;
      font-weight: 500;
      color: rgba(134, 134, 134, 1);
      line-height: 17px;
    }
    .ScanningFilterBoxContent {
      padding: 34px 10.75% 0;
      margin-top: 20px;
      height: 477px;
      background: rgba(255, 255, 255, 1);
      border-radius: 8px;
      text-align: center;
      .hostNumber {
        margin: 20px 0;
        text-align: left;
        padding-left: 16px;
        height: 50px;
        background: rgba(249, 249, 250, 1);
        border-radius: 12px;
        line-height: 50px;
        font-size: 16px;
        font-weight: 500;
        color: rgba(61, 61, 61, 1);
        em {
          margin-right: 14px;
        }
      }
      .van-checkbox {
        padding-top: 8px;
      }
      .van-cell {
        height: 50px;
        background: rgba(249, 249, 250, 1);
        border-radius: 12px;
        line-height: 50px;
        padding: 0 10px;
      }
      .van-field__label {
        width: 86px;
        padding-right: 14px;
        font-size: 16px;
        font-weight: 500;
        color: rgba(61, 61, 61, 1);
      }
      .van-field__control {
        font-size: 16px;
      }
      .ScanningFilterBoxContentTitle {
        margin-top: 14px;
        display: flex;
        p {
          font-size: 12px;
          font-weight: 500;
          color: rgba(254, 96, 59, 1);
          line-height: 17px;
        }
        .ScanningFilterBoxContentTitleSpan {
          margin-left: auto;
          font-size: 12px;
          font-weight: 500;
          color: rgba(28, 118, 254, 1);
          line-height: 17px;
          text-decoration: underline;
        }
      }
      .ScanningFilterBoxContentTitles {
        margin-top: 14px;
        display: flex;
        margin-bottom: -31px;
        p {
          font-size: 12px;
          font-weight: 500;
          color: rgba(254, 96, 59, 1);
          line-height: 17px;
        }
      }
      .ScanningFilterBoxContentButtom {
        margin-top: 51px;
        height: 50px;
        background: linear-gradient(
          180deg,
          rgba(28, 118, 254, 1) 0%,
          rgba(28, 118, 254, 1) 100%
        );
        box-shadow: 0px 10px 40px 0px rgba(28, 118, 254, 0.3);
        border-radius: 12px;
        font-size: 16px;
        font-weight: 500;
        color: rgba(255, 255, 255, 1);
        line-height: 50px;
      }
    }
  }
  .vanPopupinHand {
    width: 206px;
    padding: 30px 0;
    height: 104px;
    background: rgba(255, 255, 255, 1);
    box-shadow: 0px 14px 60px 0px rgba(0, 0, 0, 0.05);
    border-radius: 15px;
    text-align: center;
    img {
      width: 60px;
      height: 60px;
    }
    h2 {
      font-size: 16px;
