      margin-top: 22px;
    }
  }
  .vanPopupfailed {
    padding: 40px 0;
    width: 280px;
    height: 210px;
    background: rgba(255, 255, 255, 1);
    border-radius: 15px;
    text-align: center;
    img {
      width: 60px;
      height: 60px;
    }
    h2 {
      margin-top: 26px;
      margin-bottom: 4px;
      font-size: 15px;
      font-weight: 500;
      color: rgba(61, 61, 61, 1);
      line-height: 20px;
    }
    h3 {
      font-size: 13px;
      font-weight: 500;
      color: rgba(134, 134, 134, 1);
      line-height: 20px;
    }
    div {
      width: 150px;
      height: 40px;
      background: rgba(28, 118, 254, 1);
      box-shadow: 0px 12px 30px 0px rgba(28, 118, 254, 0.15);
      border-radius: 6px;
